<template>
  <div>
    <div :style="{height: specHeight + 'px'}" style="width: 100%;"></div>
    <mt-header style="padding: 0px;" title="人员定位监控">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div @click="clickHandle">
      <mt-cell is-link>
        <span slot="title">{{projname}}</span>
      </mt-cell>
    </div>
    <el-table
      :data="tableData"
      class="classtb"
      border
      cell-style="padding: 0"
      :header-cell-style="tableHeaderColor"
      style="width: 100%">
      <el-table-column
        prop="EmployeeID"
        fixed
        label="序号"
        width="60">
      </el-table-column>
      <el-table-column
        prop="UserName"
        label="姓名"
        width="80">
      </el-table-column>
      <el-table-column
        prop="Department"
        label="所属部门"
        width="100">
      </el-table-column>
      <el-table-column
        prop="POST_NAME"
        label="职务"
        width="80">
      </el-table-column>
      <el-table-column
        prop="WORK_NAME"
        label="工种"
        width="80">
      </el-table-column>
      <el-table-column
        prop="USER_PHONE"
        label="联系人电话"
        width="160">
      </el-table-column>
      <el-table-column
        prop="Address"
        label="住址"
        width="160">
      </el-table-column>
      <el-table-column
        prop="DistanceText"
        label="人员位置"
        width="100">
      </el-table-column>
      <el-table-column
        prop="DistanceText"
        label="最后活动时间"
        width="120">
      </el-table-column>
    </el-table>
    <mt-popup style="width: 100%" v-model="sheetVisible" position="bottom" class="mint-popup">
      <mt-picker style="width: 100%" :slots="projData" value-key="ProjectName" @change="onValuesChange"></mt-picker>
    </mt-popup>
  </div>
</template>
<script>

export default {
  computed: {
    projData () {
      let projSlot = [{
        flex: 1,
        textAlign: 'center',
        values: this.$store.getters.projName,
        className: 'slot1'
      }]
      return projSlot
    },
    tableData () {
      let realData = this.$store.getters.peoplePositionList[this.projKey]
      return realData
    }
  },
  data () {
    return {
      projKey: this.$store.getters.projName[0].ProjectNO.toString(),
      specHeight: this.$store.getters.statusBarHeight,
      popupVisible: false,
      sheetVisible: false,
      projname: this.$store.getters.projName[0].ProjectName
    }
  },
  methods: {
    tableHeaderColor ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return 'background-color: #F7F6Fd;color: #B6B5C2;font-weight: 500;padding: 1px;'
      }
    },
    onValuesChange (picker, values) {
      if (values[0] > values[1]) {
        picker.setSlotValue(1, values[0].ProjectName)
      }
      console.log(picker.getValues())
      this.projname = picker.getValues()[0].ProjectName
      this.projKey = picker.getValues()[0].ProjectNO.toString()
    },
    clickHandle () {
      console.log('11111111111111')
      this.sheetVisible = !this.sheetVisible
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
  .el-table--medium th {
    padding: 1px, 0;
  }
</style>
<style scoped>
  .el-table--medium td, .el-table--medium th {
    padding: 1px 0;
  }
</style>



